body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #424242
    url(https://assets.pokemon.com/static2/_ui/img/chrome/body_bg.png);
  text-decoration: none;
  list-style: none;
}

a {
  text-decoration: none;
  list-style: none;
  color: rgb(20, 18, 18);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainTitle {
  color: white;
  color: white;
  margin-bottom: 3%;
  margin-block-start: 0;
  font-size: 2.9em;
}

.rock {
  background-color: rgb(148, 81, 81);
}
.ghost {
  background-color: rgb(247, 247, 247);
}

.electric {
  background-color: #eed535;
}

.bug {
  background-color: #729f3f87;
}

.poison {
  background-color: #e0a7f6;
}

.normal {
  background-color: #f4f4f4;
}

.fairy {
  background-color: rgba(255, 192, 203, 0.863);
}

.fire {
  background-color: #fc7c239e;
}

.grass {
  background-color: #9bcc50;
}

.water {
  background-color: #4592c4;
}

.ice {
  background-color: #51c4e7;
}

.flying {
  background: linear-gradient(
    180deg,
    rgba(61, 199, 239, 1) 0%,
    rgba(189, 185, 184, 1) 67%
  );
}

.dragon {
  background: linear-gradient(
    180deg,
    rgba(83, 164, 207, 1) 10%,
    rgba(241, 110, 87, 1) 100%
  );
}

.psychic {
  background-color: #8f5894;
}

.ground {
  background-color: #ab9842;
}

.normal {
  background-color: #a4aca2;
}

.fighting {
  background-color: #d56723;
}

.ghost {
  background-color: #8d71b8b0;
}

.dark {
  background-color: #707070;
}

.steel {
  background-color: #9eb7b8;
}

.homeContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 2.5rem;
}

.pokemonContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.allContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.thumb-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;
  margin: 0.7rem;
  border: 1px solid #efefef;
  min-width: 17vh;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
  border-radius: 10%;
  position: relative;
  text-decoration: none;
  list-style: none;
}

.thumb-container:hover {
  transform: scale(1.1);
  transition: all 0.4s ease-in-out;
}

.imgContainer {
  margin-top: 1rem;
}

.characterName {
  margin-bottom: 0.2rem;
  margin-block-start: 0;
  text-transform: uppercase;
}

.thumb-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255, 255, 255, 0.3);
  position: absolute;
  top: 5%;
  width: 29%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.thumb-container img {
  width: 120px;
  height: 120px;
  margin: 1rem;
}

.thumb-container small {
  text-transform: capitalize;
  font-style: oblique;
  font-size: 0.9em;
}

.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.detail-wrapper button {
  color: rgb(22, 22, 22);
  padding: 0.5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}

.loadMore {
  background: #f5f5f5 linear-gradient(#f5f5f5, #f1f1f1) repeat-x;
  border-radius: 2px;
  border: 1px solid #c6c6c6;
  color: #444;
  padding: 0.5rem 1.5rem;
  min-width: 10%;
  margin-top: 1rem;
  border-radius: 15px 15px 15px 15px / 15px 15px 15px 15px;
  margin-bottom: 2%;
  background: #1b82b1;
  color: white;
  font-size: 1em;
}
